<template>
  <LineChartGenerator
    :chart-options="chartOptions"
    :chart-data="chartData"
    :chart-id="chartId"
    :dataset-id-key="datasetIdKey"
    :plugins="plugins"
    :css-classes="cssClasses"
    :styles="styles"
    :width="width"
    :height="height"
    :type="line"
  />
</template>

<script>
  import { Line as LineChartGenerator } from "vue-chartjs/legacy";

  import {
    Chart as ChartJS,
    Title,
    Tooltip,
    Legend,
    LineElement,
    LinearScale,
    CategoryScale,
    PointElement,
    Filler,
  } from "chart.js";

  ChartJS.register(
    Title,
    Tooltip,
    Legend,
    LineElement,
    LinearScale,
    CategoryScale,
    PointElement,
    Filler
  );

  export default {
    name: "LineChart",

    components: {
      LineChartGenerator,
    },
    props: {
      chartId: {
        type: String,
        default: "line-chart",
      },
      datasetIdKey: {
        type: String,
        default: "label",
      },
      width: {
        type: Number,
        default: 400,
      },
      height: {
        type: Number,
        default: 400,
      },
      cssClasses: {
        default: "",
        type: String,
      },
      styles: {
        type: Object,
        default: () => {},
      },
      plugins: {
        type: Array,
        default: () => [],
      },
    },

    data() {
      return {
        chartData: {
          labels: [
            "January",
            "February",
            "March",
            "April",
            "May",
            "June",
            "July",
            "August",
            "September",
            "October",
            "November",
            "December",
          ],
          datasets: [
            {
              fill: true,
              label: "2018 Sales",
              borderColor: "pink",
              backgroundColor: "#F1797F",
              data: [
                300,
                500,
                400,
                100,
                400,
                200,
                300,
                500,
                200,
                305,
                400,
                100,
              ],
            },
            // {
            //   fill: true,
            //   label: "2018 Sales",
            //   borderColor: "pink",
            //   backgroundColor: "rgba(955, 0, 0, 0.5)",
            //   data: [700, 300, 78, 750, 450, 500, 800],
            // },
          ],
        },
        chartOptions: {
          responsive: true,
          maintainAspectRatio: false,
        },
      };
    },
  };
</script>
