<template>
  <fragment>
    <div
      class="modal fade modal-01"
      id="toolShareModal"
      tabindex="-1"
      aria-labelledby="toolShareModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-dialog-centered lg">
        <div class="modal-content">
          <div class="modal-header">
            <h6>Share</h6>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            ></button>
          </div>
          <div class="modal-body px-3">
            <div class="flex-center-between">
              <div class="form-icon">
                <input
                  type="Email"
                  class="form-control"
                  id="validationTooltip01"
                  placeholder="Search..."
                />
                <i class="ic-search"></i>
              </div>

              <button
                class="btn btn-primary btn-icon lft"
                data-bs-toggle="modal"
                data-bs-target="#inviteModal"
              >
                <i class="ic-plus"></i>
                Invite New
              </button>
            </div>
            <div class="table-responsive">
              <table class="table table-01 table-striped table-action">
                <thead>
                  <th>Name</th>
                  <th>Type</th>
                  <th>Expiration Date</th>
                  <th>Active</th>
                  <th>Action</th>
                </thead>
                <tbody>
                  <tr v-for="item in tableData" :key="item.index">
                    <td>{{ item.name }}</td>
                    <td>
                      {{ item.type }}
                    </td>
                    <td>
                      {{ item.expiration }}
                    </td>
                    <td>
                      {{ item.active }}
                    </td>
                    <td>
                      <button class="btn btn-sm btn-icon-only">
                        <i class="ic-edit  text-cool-gray-600"></i>
                      </button>
                      <button class="btn btn-sm btn-icon-only">
                        <i class="ic-delete text-cool-gray-600"></i>
                      </button>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
            <Pagination />
          </div>
          <div class="modal-footer p-3">
            <button type="button" class="btn" data-bs-dismiss="modal">
              Cancel
            </button>
            <button type="button" class="btn btn-primary">
              Save
            </button>
          </div>
        </div>
      </div>
    </div>
    <div
      class="modal fade modal-01 "
      id="inviteModal"
      tabindex="-1"
      aria-labelledby="inviteModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h6>Invite New</h6>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            ></button>
          </div>
          <div class="modal-body px-3">
            <div class="p-2">
              <div class="mb-3">
                <p class="mb-2">Expiration Type</p>
                <div class="form-check form-check-inline">
                  <input
                    class="form-check-input"
                    type="radio"
                    name="inlineRadioOptions"
                    id="inlineRadio1"
                    value="option1"
                  />
                  <label class="form-check-label" for="inlineRadio1"
                    >None</label
                  >
                </div>
                <div class="form-check form-check-inline">
                  <input
                    class="form-check-input"
                    type="radio"
                    name="inlineRadioOptions"
                    id="inlineRadio2"
                    value="option2"
                  />
                  <label class="form-check-label" for="inlineRadio2"
                    >Duration</label
                  >
                </div>
                <div class="form-check form-check-inline">
                  <input
                    class="form-check-input"
                    type="radio"
                    name="inlineRadioOptions"
                    id="inlineRadio3"
                    value="option3"
                  />
                  <label class="form-check-label" for="inlineRadio2"
                    >Date</label
                  >
                </div>
              </div>
              <div class="mb-3">
                <label for="">Duration</label>
                <select class="form-select" aria-label="Default select example">
                  <option selected>Select...</option>
                  <option value="1">One</option>
                  <option value="2">Two</option>
                  <option value="3">Three</option>
                </select>
              </div>
              <div>
                <div class="form-check">
                  <input
                    class="form-check-input"
                    type="checkbox"
                    value=""
                    id="checkbox-all"
                  />
                  <label class="form-check-label" for="checkbox-all">
                    Delete after expiration
                  </label>
                </div>
              </div>
            </div>
            <div class="mb-3">
              <label for="">Devices</label>
              <div class="form-icon">
                <input
                  type="text"
                  class="form-control"
                  id="validationTooltip01"
                  placeholder="Search..."
                />
                <i class="ic-search"></i>
              </div>
            </div>
            <div class="form-check">
              <input
                class="form-check-input"
                type="checkbox"
                value=""
                id="checkbox-all"
              />
              <label class="form-check-label" for="checkbox-all">
                Email Notification
              </label>
            </div>
            <div class="mb-3 ml-4">
              <input
                type="tetx"
                class="form-control"
                id="validationTooltip01"
              />
              <small class="text-cool-gray-600 mt-1"
                >For multiple emails separate them via semicolon ex.:
                user@example.com; user1@example.com</small
              >
            </div>
            <div class="form-check">
              <input
                class="form-check-input"
                type="checkbox"
                value=""
                id="checkbox-all"
              />
              <label class="form-check-label" for="checkbox-all">
                Direct Link
              </label>
            </div>
            <div class="mb-3 ml-4">
              <div class="form-button">
                <input
                  type="text"
                  class="form-control"
                  id="validationTooltip01"
                  value="https://geotrack.com.np/sharing/3d84c89ea6d5b677c27f5f83e7816...."
                />
                <button class="btn btn-primary">
                  Copy
                </button>
              </div>
              <small class="text-cool-gray-600 mt-1"
                >You can share the direct link to your file.</small
              >
            </div>
          </div>
          <div class="modal-footer p-3">
            <button type="button" class="btn" data-bs-dismiss="modal">
              Cancel
            </button>
            <button type="button" class="btn btn-primary">
              Upgrade
            </button>
          </div>
        </div>
      </div>
    </div>
  </fragment>
</template>

<script>
  import Pagination from "../../Utility/Pagination";
  export default {
    name: "Share",
    components: { Pagination },
    data() {
      return {
        tableData: [
          {
            id: 1,
            name: "Sharing 15",
            type:
              "https://geotrack.com.np/sharing/3d84c89ea6d5b677c27f5f83e7816e63",
            expiration: "2022/10/02",
            active: "Yes",
          },
          {
            id: 2,
            name: "Ramesh Oli",
            type:
              "https://geotrack.com.np/sharing/3d84c89ea6d5b677c27f5f83e7816e63",
            expiration: "2022/10/12",
            active: "Yes",
          },
          {
            id: 3,
            name: "Sita Jha",
            type:
              "https://geotrack.com.np/sharing/3d84c89ea6d5b677c27f5f83e7816e63",
            expiration: "2022/10/22",
            active: "No",
          },
        ],
      };
    },
  };
</script>
