import Vue from "vue";
import Router from "vue-router";
import Map from "../views/Dashboard/Map";
import CustomerDashboard from "../views/Dashboard/CustomerDashboard";
import Object from "../views/Dashboard/Objects/index";
import Report from "../views/Dashboard/Report/index";
import AllObjects from "../views/Dashboard/Objects/AllObjects";
import Stopped from "../views/Dashboard/Objects/Stopped";
import Idle from "../views/Dashboard/Objects/Idle";
import Running from "../views/Dashboard/Objects/Running";
import Event from "../views/Dashboard/Event/index";
import Alert from "../views/Dashboard/Alerts/index";
import Geofencing from "../views/Dashboard/Geofencing/index";
import AddFencing from "../views/Dashboard/Geofencing/AddFencing";
import POI from "../views/Dashboard/POI/index";
import AddPoi from "../views/Dashboard/POI/AddPoi";
import Route from "../views/Dashboard/Route/index";
import AddRoute from "../views/Dashboard/Route/AddRoute";
import History from "../views/Dashboard/History/index";

Vue.use(Router);

export default new Router({
  linkExactActiveClass: "active",
  routes: [
    {
      path: "/",
      name: "Map",
      component: Map,
      children: [
        {
          path: "/",
          name: "Object",
          component: Object,
          children: [
            {
              path: "/",
              name: "AllObjects",
              component: AllObjects,
            },
            {
              path: "/object/running",
              name: "Running",
              component: Running,
            },
            {
              path: "/object/idle",
              name: "Idle",
              component: Idle,
            },
            {
              path: "/object/stopped",
              name: "Stopped",
              component: Stopped,
            },
          ],
        },
        {
          path: "/event",
          name: "Event",
          component: Event,
        },
        {
          path: "/alert",
          name: "Alert",
          component: Alert,
        },
        {
          path: "/geo-fencing",
          name: "Geofencing",
          component: Geofencing,
        },
        {
          path: "/add-fencing",
          name: "AddFencing",
          component: AddFencing,
        },
        {
          path: "/poi",
          name: "POI",
          component: POI,
        },
        {
          path: "/add-poi",
          name: "AddPoi",
          component: AddPoi,
        },
        {
          path: "/routes",
          name: "Route",
          component: Route,
        },
        {
          path: "/add-route",
          name: "AddRoute",
          component: AddRoute,
        },
        {
          path: "/history",
          name: "History",
          component: History,
        },
      ],
    },
    {
      path: "/report",
      name: "Report",
      component: Report,
    },
    {
      path: "/dashboard",
      name: "CustomerDashboard",
      component: CustomerDashboard,
    },
  ],
});
