<template>
  <flex-layout styleClasses="py-3">
    <div class="px-3">
      <h6 class="text-primary mb-3">Add</h6>
      <div class="form-icon">
        <input
          type="Email"
          class="form-control"
          id="validationTooltip01"
          placeholder="Search..."
        />
        <i class="ic-search"></i>
      </div>
    </div>
    <div class="alert alert-blue-100 my-3">
      <i class="ic-info mr-2"></i>
      <small>Please draw a polygon on the map.</small>
    </div>
    <div class="px-3">
      <div class="mb-3">
        <label for="">Name</label>
        <input type="tetx" class="form-control" id="validationTooltip01" />
      </div>
      <div class="mb-3">
        <label for="">Type</label>
        <select class="form-select" aria-label="Default select example">
          <option selected>Select...</option>
          <option value="1">One</option>
          <option value="2">Two</option>
          <option value="3">Three</option>
        </select>
      </div>
      <div class="mb-3">
        <label for="">Device</label>
        <select class="form-select" aria-label="Default select example">
          <option selected>Select...</option>
          <option value="1">One</option>
          <option value="2">Two</option>
          <option value="3">Three</option>
        </select>
      </div>
      <div class="mb-3">
        <label for="">Group</label>
        <select class="form-select" aria-label="Default select example">
          <option selected>Select...</option>
          <option value="1">One</option>
          <option value="2">Two</option>
          <option value="3">Three</option>
        </select>
      </div>
      <div class="mb-3">
        <label for="">Background Color</label>
        <div class="form-control form-icon custom-color">
          <input type="color" value="#6045DC" id="color-picker" />
          <small id="color-code"></small>
          <i class="ic-bucket" id="color-icon"></i>
        </div>
      </div>
      <div class="flex-end mt-2">
        <router-link class="btn btn-sm mr-2" to="geo-fencing"
          >Cancel</router-link
        >
        <button class="btn btn-primary btn-sm">Save</button>
      </div>
    </div>
  </flex-layout>
</template>

<script>
  import FlexLayout from "../../../components/Layout/FlexLayout";

  export default {
    name: "AddFencing",
    components: { FlexLayout },

    data() {
      return {
        groupList: [
          {
            id: "1",
            title: "Baneshwor group (20)",
            groupItems: [
              {
                title: "LU A A A 4813",
              },
              {
                title: "OBD 9801108608",
              },
              {
                title: "OBD Testing",
              },
            ],
          },
          {
            id: "2",
            title: "Bhaktapur group (20)",
            groupItems: [
              {
                title: "LU A A A 4813",
              },
              {
                title: "OBD 9801108608",
              },
              {
                title: "OBD Testing",
              },
            ],
          },
        ],
      };
    },

    methods: {
      HandlerFunction(event) {
        event.target.parentElement.classList.toggle("active");
      },
    },
    mounted() {
      const input = document.getElementById("color-picker");
      const colorCode = document.getElementById("color-code");
      const colorIcon = document.getElementById("color-icon");

      setColor();
      input.addEventListener("input", setColor);

      function setColor() {
        colorCode.innerHTML = input.value;
        colorIcon.style.color = input.value;
      }
    },
  };
</script>
