<template>
  <div class="app-height">
    <ChangePassword />
    <DevicePlans />
    <flex-layout>
      <div class="sidebar-01" :class="mainSidebarHide ? '' : 'toggled'">
        <AppSidebar />
        <MainSidebar />
        <button class="btn btn-sidebar-toggler" @click="hideNotice">
          <i :class="'ic-chevron-' + (mainSidebarHide ? 'left' : 'right')"></i>
        </button>
      </div>
      <div class="flex-grow-1 overflow-hidden">
        <iframe
          src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3532.952582704954!2d85.30525301506167!3d27.687860382799947!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x39eb19973263c4af%3A0xc7232e02d94b49b7!2sInfoDevelopers!5e0!3m2!1sen!2snp!4v1642992372678!5m2!1sen!2snp"
          style="border: 0; height: 100%; width: 100%"
          class="map"
          allowfullscreen=""
          loading="lazy"
        ></iframe>
      </div>
      <div class="sidebar-02" :class="vehicleSidebarHide ? '' : 'toggled'">
        <ActionBar />
        <button
          class="btn btn-sidebar-toggler"
          @click="hideVehicleSidebar"
          :class="pathName === '/' ? 'd-block' : 'd-none'"
        >
          <i
            :class="'ic-chevron-' + (vehicleSidebarHide ? 'right' : 'left')"
          ></i>
        </button>
        <VehicleSidebar :pathName="pathName" />
      </div>
      <HistoryChart />
    </flex-layout>
  </div>
</template>

<script>
  import AppSidebar from "../../components/Sidebar/AppSidebar";
  import ChangePassword from "../../components/Modal/ChangePassword";
  import DevicePlans from "../../components/Modal/DevicePlans";
  import MainSidebar from "../../components/Sidebar/MainSidebar";
  import VehicleSidebar from "../../components/Sidebar/VehicleSidebar";
  import ActionBar from "../../components/Sidebar/ActionBar";
  import FlexLayout from "../../components/Layout/FlexLayout";
  // import AbsoluteLayout from "../../components/Layout/AbsoluteLayout";
  import HistoryChart from "../Dashboard/History/HistoryChart";

  export default {
    name: "Map",
    components: {
      AppSidebar,
      ActionBar,
      MainSidebar,
      FlexLayout,
      // AbsoluteLayout,
      VehicleSidebar,
      ChangePassword,
      DevicePlans,
      HistoryChart,
    },
    data() {
      return {
        pathName: "/",
        mainSidebarHide: true,
        vehicleSidebarHide: true,
      };
    },
    methods: {
      hideNotice() {
        this.mainSidebarHide = !this.mainSidebarHide;
      },
      hideVehicleSidebar() {
        this.vehicleSidebarHide = !this.vehicleSidebarHide;
      },
    },
    watch: {
      $route() {
        this.pathName = this.$route.path;
      },
    },
  };
</script>
