<template>
  <flex-layout styleClasses="py-3">
    <div class="px-3">
      <div class="flex-center-between mb-3">
        <h6 class="text-primary">Alerts</h6>
        <button class="btn btn-primary p-1">
          <i class="ic-plus"></i>
        </button>
      </div>
      <div class="form-icon mb-3">
        <input
          type="search"
          class="form-control"
          id="validationTooltip01"
          placeholder="Search..."
        />
        <i class="ic-search"></i>
      </div>
    </div>
    <ul class="list-alert flex-grow-1 scrollable px-3">
      <li v-for="item in alertList" :key="item.index">
        <div class="form-check">
          <input
            class="form-check-input"
            type="checkbox"
            value=""
            id="checkbox-all"
          />
          <label class="form-check-label" for="checkbox-all">
            {{ item.title }}
          </label>
        </div>
        <div class="action">
          <button
            class="btn btn-icon-only me-2"
            data-bs-toggle="modal"
            data-bs-target="#editAlertModal"
          >
            <i class="ic-edit"></i>
          </button>
          <button class="btn btn-icon-only"><i class="ic-delete"></i></button>
        </div>
      </li>
    </ul>
    <portal to="modal">
      <div
        class="modal fade modal-01"
        id="editAlertModal"
        tabindex="-1"
        aria-labelledby="editAlertModalLabel"
        aria-hidden="true"
      >
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-header">
              <h6>Edit Alert</h6>
              <button
                type="button"
                class="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <div class="modal-body px-3">
              <div class="accordion accordion-01" id="accordionExample">
                <div class="accordion-item">
                  <div
                    class="accordion-header"
                    id="headingOne"
                    data-bs-toggle="collapse"
                    data-bs-target="#collapseOne"
                    aria-expanded="true"
                    aria-controls="collapseOne"
                  >
                    <h6 class="index">1</h6>
                    <h6>Select Device</h6>
                  </div>
                  <div
                    id="collapseOne"
                    class="accordion-collapse collapse show"
                    aria-labelledby="headingOne"
                    data-bs-parent="#accordionExample"
                  >
                    <div class="accordion-body">
                      <div class="mb-3">
                        <label for="">Name</label>
                        <input
                          type="text"
                          class="form-control"
                          id="validationTooltip01"
                        />
                      </div>
                      <div class="mb-3">
                        <label for="">Devices</label>
                        <div class="form-icon">
                          <input
                            type="text"
                            class="form-control"
                            id="validationTooltip01"
                            placeholder="Search..."
                          />
                          <i class="ic-search"></i>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="accordion-item">
                  <div
                    class="accordion-header"
                    id="headingTwo"
                    data-bs-toggle="collapse"
                    data-bs-target="#collapseTwo"
                    aria-expanded="false"
                    aria-controls="collapseTwo"
                  >
                    <h6 class="index">2</h6>
                    <h6>Select Type of Condition</h6>
                  </div>
                  <div
                    id="collapseTwo"
                    class="accordion-collapse collapse"
                    aria-labelledby="headingTwo"
                    data-bs-parent="#accordionExample"
                  >
                    <div class="accordion-body">
                      <div class="mb-3">
                        <label for="">Type</label>
                        <select
                          class="form-select"
                          aria-label="Default select example"
                        >
                          <option selected>Select...</option>
                          <option value="1">One</option>
                          <option value="2">Two</option>
                          <option value="3">Three</option>
                        </select>
                      </div>
                      <div class="mb-3">
                        <label for="">Overspeed (kph)</label>
                        <input
                          type="tetx"
                          class="form-control"
                          id="validationTooltip01"
                        />
                      </div>
                    </div>
                  </div>
                </div>
                <div class="accordion-item">
                  <div
                    class="accordion-header"
                    id="headingThree"
                    data-bs-toggle="collapse"
                    data-bs-target="#collapseThree"
                    aria-expanded="false"
                    aria-controls="collapseThree"
                  >
                    <h6 class="index">3</h6>
                    <h6>Select Geofencing</h6>
                  </div>
                  <div
                    id="collapseThree"
                    class="accordion-collapse collapse"
                    aria-labelledby="headingThree"
                    data-bs-parent="#accordionExample"
                  >
                    <div class="accordion-body">
                      <div class="align-center mb-2">
                        <div class="form-check mr-3">
                          <input
                            class="form-check-input"
                            type="checkbox"
                            value=""
                            id="checkbox-all"
                          />
                          <label class="form-check-label" for="checkbox-all">
                            Zone in
                          </label>
                        </div>
                        <div class="form-check">
                          <input
                            class="form-check-input"
                            type="checkbox"
                            value=""
                            id="checkbox-all"
                          />
                          <label class="form-check-label" for="checkbox-all">
                            Zone Out
                          </label>
                        </div>
                      </div>
                      <div class="mb-3">
                        <label for="">Devices</label>
                        <div class="form-icon">
                          <input
                            type="text"
                            class="form-control"
                            id="validationTooltip01"
                            placeholder="Search..."
                          />
                          <i class="ic-search"></i>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="accordion-item">
                  <div
                    class="accordion-header"
                    id="headingTwo"
                    data-bs-toggle="collapse"
                    data-bs-target="#collapseFour"
                    aria-expanded="false"
                    aria-controls="collapseFour"
                  >
                    <h6 class="index">4</h6>
                    <h6>Schedule Alert Time</h6>
                  </div>
                  <div
                    id="collapseFour"
                    class="accordion-collapse collapse"
                    aria-labelledby="headingFour"
                    data-bs-parent="#accordionExample"
                  >
                    <div class="accordion-body">
                      <div class="mb-3">
                        <label for="" class="mb-2">Select Days</label>
                        <div class="custom-radio">
                          <input
                            label="Weekend"
                            type="radio"
                            id="Weekend"
                            name="period"
                            value="Weekend"
                            checked
                          />
                          <input
                            label="Workdays"
                            type="radio"
                            id="Workdays"
                            name="period"
                            value="Workdays"
                          />
                          <input
                            label="Always"
                            type="radio"
                            id="Always"
                            name="period"
                            value="Always"
                          />
                          <input
                            label="Sunday"
                            type="radio"
                            id="Sunday"
                            name="period"
                            value="Sunday"
                          />
                          <input
                            label="Monday"
                            type="radio"
                            id="Monday"
                            name="period"
                            value="Monday"
                          />
                          <input
                            label="Tuesday"
                            type="radio"
                            id="Tuesday"
                            name="period"
                            value="Tuesday"
                          />
                          <input
                            label="Wednesday"
                            type="radio"
                            id="Wednesday"
                            name="period"
                            value="Wednesday"
                          />
                          <input
                            label="Thursday"
                            type="radio"
                            id="Thursday"
                            name="period"
                            value="Thursday"
                          />
                          <input
                            label="Friday"
                            type="radio"
                            id="Friday"
                            name="period"
                            value="Friday"
                          />
                        </div>
                      </div>
                      <div class="mb-3">
                        <label for="" class="mb-2">Select Time</label>
                        <div class="align-center">
                          <div class="form-timer mr-3">
                            <div class="flex-column">
                              <button class="btn btn-xs btn-icon-only">
                                <i class="ic-chevron-top"></i>
                              </button>
                              <input
                                type="text"
                                class="form-control"
                                value="00"
                                onkeydown="return false"
                              />
                              <button class="btn btn-xs btn-icon-only">
                                <i class="ic-chevron-bottom"></i>
                              </button>
                            </div>
                            <div class="flex-column">
                              <button class="btn btn-xs btn-icon-only">
                                <i class="ic-chevron-top"></i>
                              </button>
                              <input
                                type="text"
                                class="form-control"
                                value="00"
                                onkeydown="return false"
                              />
                              <button class="btn btn-xs btn-icon-only">
                                <i class="ic-chevron-bottom"></i>
                              </button>
                            </div>
                            <div class="flex-column">
                              <button class="btn btn-xs btn-icon-only">
                                <i class="ic-chevron-top"></i>
                              </button>
                              <input
                                type="text"
                                class="form-control"
                                value="PM"
                                onkeydown="return false"
                              />
                              <button class="btn btn-xs btn-icon-only">
                                <i class="ic-chevron-bottom"></i>
                              </button>
                            </div>
                          </div>
                          <button class="btn btn-primary btn-sm btn-icon lft">
                            <i class="ic-plus"></i>
                            Add
                          </button>
                        </div>
                      </div>
                      <ul class="list-flex list mb-3">
                        <li
                          class="badge-outline-cool-gray-200 badge__md"
                          v-for="item in scheduleList"
                          :key="item.index"
                        >
                          <span class="text-secondary">
                            {{ item.day }}
                          </span>
                          <span class="text-cool-gray-800">
                            , {{ item.time }}
                          </span>

                          <button class="btn btn-icon-only ml-2">
                            <i class="ic-close small text-cool-gray-600"></i>
                          </button>
                        </li>
                      </ul>
                    </div>
                  </div>
                </div>
                <div class="accordion-item">
                  <div
                    class="accordion-header"
                    id="headingFive"
                    data-bs-toggle="collapse"
                    data-bs-target="#collapseFive"
                    aria-expanded="false"
                    aria-controls="collapseFive"
                  >
                    <h6 class="index">5</h6>
                    <h6>Notification Setting</h6>
                  </div>
                  <div
                    id="collapseFive"
                    class="accordion-collapse collapse"
                    aria-labelledby="headingFive"
                    data-bs-parent="#accordionExample"
                  >
                    <div class="accordion-body">
                      <div class="form-check mb-3">
                        <input
                          class="form-check-input"
                          type="checkbox"
                          value=""
                          id="checkbox-all"
                        />
                        <label class="form-check-label" for="checkbox-all">
                          Select All
                        </label>
                      </div>
                      <div class="align-center mb-3">
                        <div class="form-check">
                          <input
                            class="form-check-input"
                            type="checkbox"
                            value=""
                            id="checkbox-all"
                          />
                          <label class="form-check-label" for="checkbox-all">
                            Sound Notification
                          </label>
                        </div>
                        <div class="form-check mx-3">
                          <input
                            class="form-check-input"
                            type="checkbox"
                            value=""
                            id="checkbox-all"
                          />
                          <label class="form-check-label" for="checkbox-all">
                            Push Notification
                          </label>
                        </div>
                        <div class="form-check">
                          <input
                            class="form-check-input"
                            type="checkbox"
                            value=""
                            id="checkbox-all"
                          />
                          <label class="form-check-label" for="checkbox-all">
                            Auto hide popup
                          </label>
                        </div>
                      </div>

                      <div class="multiselect mb-3">
                        <label>Alert Sound</label>

                        <multiselect
                          v-model="value"
                          :placeholder="value.title"
                          label="title"
                          track-by="title"
                          :options="options"
                          :custom-label="customLabel"
                          :show-labels="false"
                          :close-on-select="false"
                        >
                          <template slot="singleLabel" slot-scope="props">
                            <p>{{ props.option.title }}</p>
                          </template>
                          <template slot="option" slot-scope="props">
                            <audio id="audio" :src="props.option.audio"></audio>
                            <div class="flex-center-between">
                              <small>{{ props.option.title }}</small>
                              <div class="icon-music" @click="play">
                                <i
                                  :class="
                                    'ic-' + (playMusic ? 'pause' : 'play ml-1')
                                  "
                                ></i>
                              </div>
                            </div>
                          </template>
                        </multiselect>
                      </div>
                      <div class="form-check">
                        <input
                          class="form-check-input"
                          type="checkbox"
                          value=""
                          id="checkbox-all"
                        />
                        <label class="form-check-label" for="checkbox-all">
                          Email Notification
                        </label>
                      </div>
                      <div class="mb-3 ml-4">
                        <input
                          type="text"
                          class="form-control"
                          id="validationTooltip01"
                        />
                        <small class="text-cool-gray-600 mt-1"
                          >For multiple emails separate them via semicolon ex.:
                          user@example.com; user1@example.com</small
                        >
                      </div>
                      <div class="form-check">
                        <input
                          class="form-check-input"
                          type="checkbox"
                          value=""
                          id="checkbox-all"
                        />
                        <label class="form-check-label" for="checkbox-all">
                          Webhook notification
                        </label>
                      </div>
                      <div class="mb-3 ml-4">
                        <input
                          type="text"
                          class="form-control"
                          id="validationTooltip01"
                        />
                        <small class="text-cool-gray-600 mt-1"
                          >The URL you would like event data posted to.</small
                        >
                      </div>
                      <div class="form-check">
                        <input
                          class="form-check-input"
                          type="checkbox"
                          value=""
                          id="checkbox-all"
                        />
                        <label class="form-check-label" for="checkbox-all">
                          Email notification with sharing link
                        </label>
                      </div>
                      <div class="mb-3 ml-4">
                        <input
                          type="tetx"
                          class="form-control"
                          id="validationTooltip01"
                        />
                        <small class="text-cool-gray-600 mt-1"
                          >For multiple emails separate them via semicolon ex.:
                          user@example.com; user1@example.com</small
                        >
                      </div>
                    </div>
                  </div>
                </div>
                <div class="accordion-item">
                  <div
                    class="accordion-header"
                    id="headingSix"
                    data-bs-toggle="collapse"
                    data-bs-target="#collapseSix"
                    aria-expanded="false"
                    aria-controls="collapseSix"
                  >
                    <h6 class="index">6</h6>
                    <h6>Add Command</h6>
                  </div>
                  <div
                    id="collapseSix"
                    class="accordion-collapse collapse"
                    aria-labelledby="headingSix"
                    data-bs-parent="#accordionExample"
                  >
                    <div class="accordion-body">
                      <div class="mb-3">
                        <div class="form-check">
                          <input
                            class="form-check-input"
                            type="checkbox"
                            value=""
                            id="checkbox-all"
                          />
                          <label class="form-check-label" for="checkbox-all">
                            Active
                          </label>
                        </div>
                      </div>
                      <div class="mb-3">
                        <label for="">Type</label>
                        <select
                          class="form-select"
                          aria-label="Default select example"
                        >
                          <option selected>Select...</option>
                          <option value="1">One</option>
                          <option value="2">Two</option>
                          <option value="3">Three</option>
                        </select>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="modal-footer p-3">
              <button type="button" class="btn" data-bs-dismiss="modal">
                Cancel
              </button>
              <button type="button" class="btn btn-primary">Upgrade</button>
            </div>
          </div>
        </div>
      </div>
    </portal>
  </flex-layout>
</template>

<script>
  import FlexLayout from "../../../components/Layout/FlexLayout";
  import logo from "../../../assets/img/logo.png";
  import Multiselect from "vue-multiselect";
  export default {
    name: "Alert",
    components: { FlexLayout, Multiselect },

    data() {
      return {
        playMusic: false,
        value: {
          title: "Explorer",
          img: logo,
        },
        options: [
          {
            title: "Space Pirate",
            audio:
              "http://commondatastorage.googleapis.com/codeskulptor-assets/Epoq-Lepidoptera.ogg",
          },
          {
            title: "Merchant",
            audio:
              "http://commondatastorage.googleapis.com/codeskulptor-assets/Epoq-Lepidoptera.ogg",
          },
          {
            title: "Explorer",
            audio:
              "http://commondatastorage.googleapis.com/codeskulptor-assets/Epoq-Lepidoptera.ogg",
          },
          {
            title: "Miner",
            audio:
              "http://commondatastorage.googleapis.com/codeskulptor-assets/Epoq-Lepidoptera.ogg",
          },
        ],
        alertList: [
          {
            id: 1,
            title: "Fuel Theft",
          },
          {
            id: 2,
            title: "Speeding",
          },
          {
            id: 3,
            title: "Device Expired",
          },
          {
            id: 4,
            title: "Power Cut",
          },
          {
            id: 5,
            title: "Ignitation Test",
          },
        ],
        scheduleList: [
          {
            id: 1,
            time: "12:00 am",
            day: "Sunday",
          },
          {
            id: 2,
            time: "12:00 am",
            day: "Monday",
          },
          {
            id: 3,
            time: "12:00 am",
            day: "Thursday",
          },
        ],
      };
    },
    methods: {
      play() {
        var audio = document.getElementById("audio");
        this.playMusic = !this.playMusic;
        if (audio.paused) {
          audio.play();
        } else {
          audio.pause();
        }
      },
    },
    mounted() {},
  };
</script>
