<template>
  <flex-layout styleClasses="py-3 page-history">
    <h6 class="text-primary mb-3 px-3">History</h6>
    <div class="align-center px-3 mb-3">
      <div class="form-icon ">
        <input
          type="search"
          class="form-control"
          id="validationTooltip01"
          placeholder="Search..."
        />
        <i class="ic-search"></i>
      </div>
      <div class="dropdown-01">
        <button class="btn btn-outline-gray ml-2" @click="toggleFilter">
          <i class="ic-filter h6 line__neutral"></i>
        </button>
        <div class="filter lfty" :class="showFilter && 'show'">
          <p class="font-semibold mb-4">Filter</p>
          <form action="">
            <div class="mb-3">
              <label for="">Stops</label>
              <select class="form-select" aria-label="Default select example">
                <option selected>Select...</option>
                <option value="1">One</option>
                <option value="2">Two</option>
                <option value="3">Three</option>
              </select>
            </div>
            <div class="mb-4">
              <div class="form-check mr-2">
                <input
                  class="form-check-input"
                  type="checkbox"
                  value=""
                  id="checkbox-all"
                />
                <label class="form-check-label" for="checkbox-all">
                  Snap to Road
                </label>
              </div>
            </div>

            <div class="flex-end">
              <button class="btn mr-3" @click="toggleFilter">
                Cancel
              </button>
              <button class="btn btn-primary" @click="toggleFilter">
                Apply
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
    <VueSlickCarousel v-bind="setting" class="slider-history">
      <div class="nav-item">
        Today
        <div class="form-icon rft">
          <input
            type="date"
            class="form-control"
            id="exampleFormControlInput1"
          />
          <i class="ic-chevron-bottom"></i>
        </div>
      </div>
      <div class="nav-item">
        12th Jan, 2022
      </div>
      <div class="nav-item">
        11th Jan, 2022
      </div>
      <template #prevArrow="arrowOption">
        <div class="custom-arrow">
          {{ arrowOption.currentSlide }}/{{ arrowOption.slideCount }}
        </div>
      </template>
      <template #nextArrow="arrowOption">
        <div class="custom-arrow">
          {{ arrowOption.currentSlide }}/{{ arrowOption.slideCount }}
        </div>
      </template>
    </VueSlickCarousel>
    <div class="dropdown-01 divider position-relative p-3">
      <a
        class="align-center"
        type="button"
        id="dropdownMenuButton1"
        data-bs-toggle="dropdown"
        aria-expanded="false"
      >
        <i class="ic-car h6"></i>
        <small class="font-semibold flex-grow-1 mx-2">
          Not Selected
        </small>
        <i class="ic-chevron-bottom"></i>
      </a>
      <ul class="dropdown-menu" aria-labelledby="dropdownMenuButton1">
        <li v-for="item in vehicleList" :key="item.index">
          <a class="dropdown-item" href="#"
            ><i :class="'ic-car ' + item.status"></i>{{ item.vehicle }}</a
          >
        </li>
      </ul>
    </div>
    <!-- <div class="px-3 text-center mtop-32">
      <i class="ic-car h3 mb-3 text-cool-gray-600"></i>
      <small class="text-cool-gray-600">
        The device is not selected yet. Start by choosing one to view history.
      </small>
    </div> -->
    <div class="activity flex-grow-1 scrollable">
      <di6v class="activity-log" v-for="item in activityList" :key="item.index">
        <template v-if="item.status === 'initiation'">
          <div class="first"></div>
        </template>
        <template v-else-if="item.status === 'parked'">
          <div class="icon-sm parked"><i class="ic-parking"></i></div>
        </template>
        <template v-else>
          <div class="icon-sm running"><i class="ic-car"></i></div>
        </template>
        <div>
          <small>
            {{ item.location }}
          </small>
          <small class="text-cool-gray-600 my-1">{{ item.timing }}</small>
          <div class="align-center">
            <div class="badge-outline-cool-gray-300 align-center me-2">
              <div class="icon-xs bg-blue-50 me-1">
                <i class="ic-clock text-blue"></i>
              </div>
              <span class="text-blue">
                {{ item.duration }}
              </span>
            </div>
            <div class="badge-outline-cool-gray-300 align-center">
              <div class="icon-xs bg-green-100 me-1">
                <i class="ic-forward text-success"></i>
              </div>
              <span class="text-success">
                {{ item.distance }}
              </span>
            </div>
          </div>
        </div>
      </di6v>
    </div>
  </flex-layout>
</template>

