    components: { FlexLayout, VueSlickCarousel },

    data() {
      return {
        showFilter: false,
        setting: {
          dots: false,
          infinite: false,
          slidesToShow: 1,
          slidesToScroll: 1,
          rtl: true,
        },
        activityList: [
          {
            id: 1,
            location:
              "Benimanipur, Nawalparasi, Lumbini, Western Development Region, Nepal",
            timing: "Reached at 02:57 PM",
            duration: "4h 21min",
            distance: "104.2 KM",
            status: "initiation",
          },
          {
            id: 2,
            location:
              "Benimanipur, Nawalparasi, Lumbini, Western Development Region, Nepal",
            timing: "Reached at 02:57 PM",
            duration: "4h 21min",
            distance: "104.2 KM",
            status: "parked",
          },
          {
            id: 3,
            location:
              "Benimanipur, Nawalparasi, Lumbini, Western Development Region, Nepal",
            timing: "Reached at 02:57 PM",
            duration: "4h 21min",
            distance: "104.2 KM",
            status: "running",
          },
          {
            id: 4,
            location:
              "Benimanipur, Nawalparasi, Lumbini, Western Development Region, Nepal",
            timing: "Reached at 02:57 PM",
            duration: "4h 21min",
            distance: "104.2 KM",
            status: "parked",
          },
          {
            id: 5,
            location:
              "Benimanipur, Nawalparasi, Lumbini, Western Development Region, Nepal",
            timing: "Reached at 02:57 PM",
            duration: "4h 21min",
            distance: "104.2 KM",
            status: "initiation",
          },
        ],
        vehicleList: [
          {
            id: 1,
            vehicle: "3300",
            status: "red",
          },
          {
            id: 2,
            vehicle: "3299",
            status: "green",
          },
          {
            id: 3,
            vehicle: "3268",
            status: "red",
          },
        ],
      };
    },

    methods: {
      toggleFilter() {
        this.showFilter = !this.showFilter;
      },
    },
  };
</script>

<style scoped>
  .spacing__vertical > :not(:last-child) {
    margin-bottom: 1rem;
  }
</style>
