<template>
  <flex-layout styleClasses="p-3">
    <div class="flex-center-between mb-3">
      <h6 class="font-weight-bold">Objects</h6>
      <button
        class="btn btn-primary p-1"
        data-bs-toggle="modal"
        data-bs-target="#newObjectModal"
      >
        <i class="ic-plus"></i>
      </button>
    </div>

    <div class="form-icon mb-3">
      <input
        type="Email"
        class="form-control"
        id="validationTooltip01"
        placeholder="Search..."
      />
      <i class="ic-search"></i>
    </div>
    <div class="overfow-hidden">
      <VueSlickCarousel v-bind="setting" class="slider-vehicle-status mb-3">
        <li class="nav-item">
          <router-link class="nav-link" to="/">
            All
          </router-link>
        </li>
        <li class="nav-item">
          <router-link class="nav-link" to="/object/running">
            Running
          </router-link>
        </li>
        <li class="nav-item">
          <router-link class="nav-link" to="/object/idle">
            Idle
          </router-link>
        </li>
        <li class="nav-item">
          <router-link class="nav-link" to="/object/stopped">
            Stopped
          </router-link>
        </li>
      </VueSlickCarousel>
    </div>
    <div class="collapse-02 offset__xaxis mb-3">
      <div class="collapse-item">
        <div
          class="collapse-toggler"
          data-bs-toggle="collapse"
          href="'#collapse-expired"
          role="button"
          aria-expanded="true"
          aria-controls="'collapse-expired"
        >
          <i class="ic-warning h6"></i>
          <small class="font-semibold flex-grow-1 mx-2">
            Expired (20)
          </small>
        </div>
        <ul class="list collapse show" id="collapse-expired">
          <li>
            <div class="collapse-list-item">
              <small>OBD 9801108608</small>
              <button
                class="btn btn-blue btn-sm btn__rounded text-white"
                @click="showToast"
              >
                Renew
              </button>
            </div>
          </li>
          <li>
            <div class="collapse-list-item">
              <small>OBD 9801108608</small>
              <button
                class="btn btn-blue btn-sm btn__rounded text-white"
                @click="showDangerToast"
              >
                Renew
              </button>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <div class="flex-grow-1 position-relative">
      <absolute-layout>
        <router-view></router-view>
      </absolute-layout>
    </div>
    <!-- <button
      class="btn btn-primary"
      data-bs-toggle="modal"
      data-bs-target="#confirmationModal"
    >
      Confirmation Modal
    </button> -->
    <portal to="modal">
      <NewObject />
      <EditObject />
    </portal>
  </flex-layout>
</template>

<script>
  import VueSlickCarousel from "vue-slick-carousel";
  import FlexLayout from "../../../components/Layout/FlexLayout";
  import AbsoluteLayout from "../../../components/Layout/AbsoluteLayout";
  import NewObject from "../../../components/Modal/NewObject";
  import EditObject from "../../../components/Modal/EditObject";
  // import MyToastComponent from "../../../components/toast/MyToastComponent";
  export default {
    name: "Object",
    components: {
      VueSlickCarousel,
      FlexLayout,
      AbsoluteLayout,
      NewObject,
      EditObject,
    },

    data() {
      return {
        setting: {
          dots: false,
          infinite: false,
          slidesToShow: 3,
          slidesToScroll: 1,
          variableWidth: true,
        },
        groupList: [
          {
            id: "1",
            title: "Baneshwor group (20)",
            groupItems: [
              {
                title: "LU A A A 4813",
                online: true,
                speed: 0,
              },
              {
                title: "OBD 9801108608",
                online: false,
                speed: 0,
              },
              {
                title: "OBD Testing",
                online: true,
                speed: 0,
              },
            ],
          },
          {
            id: "2",
            title: "Bhaktapur group (20)",
            groupItems: [
              {
                title: "LU A A A 4813",
                online: true,
                speed: 0,
              },
              {
                title: "OBD 9801108608",
                online: false,
                speed: 0,
              },
              {
                title: "OBD Testing",
                online: true,
                speed: 0,
              },
            ],
          },
          {
            id: "5",
            title: "Lalitpur group (20)",
            groupItems: [
              {
                title: "LU A A A 4813",
                online: true,
                speed: 0,
              },
              {
                title: "OBD 9801108608",
                online: false,
                speed: 0,
              },
              {
                title: "OBD Testing",
                online: true,
                speed: 0,
              },
            ],
          },
          {
            id: "4",
            title: "Thankot group (20)",
            groupItems: [
              {
                title: "LU A A A 4813",
                online: true,
                speed: 0,
              },
              {
                title: "OBD 9801108608",
                online: false,
                speed: 0,
              },
              {
                title: "OBD Testing",
                online: true,
                speed: 0,
              },
            ],
          },
        ],
      };
    },
    methods: {
      HandlerFunction(event) {
        event.target.parentElement.classList.toggle("active");
      },
      showToast() {
        this.$toast.success("Successfully Updated", {
          position: "bottom-right",
          timeout: 5000,
          closeOnClick: true,
          pauseOnFocusLoss: true,
          pauseOnHover: true,
          draggable: true,
          draggablePercent: 0.6,
          showCloseButtonOnHover: false,
          hideProgressBar: true,
          closeButton: "button",
          icon: {
            iconClass: "ic-check-circle text-success",
            iconChildren: "",
            iconTag: "i",
          },
          rtl: false,
        });
      },
      showDangerToast() {
        this.$toast.error("Update Failed", {
          position: "bottom-right",
          timeout: 5000,
          closeOnClick: true,
          pauseOnFocusLoss: true,
          pauseOnHover: true,
          draggable: true,
          draggablePercent: 0.6,
          showCloseButtonOnHover: false,
          hideProgressBar: true,
          closeButton: "button",
          icon: {
            iconClass: "ic-close-circle text-danger",
            iconChildren: "",
            iconTag: "i",
          },
          rtl: false,
        });
      },
    },
    mounted() {
      this.pathName = this.$route.path;
    },
  };
</script>

