<template>
  <fragment>
    <div
      class="modal fade modal-01"
      id="toolTaskModal"
      tabindex="-1"
      aria-labelledby="toolTaskModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-dialog-centered lg">
        <div class="modal-content">
          <div class="modal-header">
            <h6>Task</h6>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            ></button>
          </div>
          <div class="modal-body px-3">
            <div class="flex-center-between">
              <div class="form-icon">
                <input
                  type="Email"
                  class="form-control"
                  id="validationTooltip01"
                  placeholder="Search..."
                />
                <i class="ic-search"></i>
              </div>
              <div class="align-center">
                <button class="btn btn-outline-primary mr-2">
                  <i class="ic-delete"></i> Delete
                </button>
                <div class="dropdown-01">
                  <button
                    class="btn btn-outline-primary mr-2"
                    @click="toggleFilter"
                  >
                    <i class="ic-filter h6 line__neutral"></i>
                  </button>
                  <div class="filter" :class="showFilter && 'show'">
                    <p class="font-semibold mb-4">Filter</p>
                    <form action="">
                      <div class="mb-3">
                        <label for="">Devices</label>
                        <div class="form-icon">
                          <input
                            type="text"
                            class="form-control"
                            id="validationTooltip01"
                            placeholder="Search..."
                          />
                          <i class="ic-search"></i>
                        </div>
                      </div>
                      <div class="mb-3">
                        <label for="">Status</label>
                        <select
                          class="form-select"
                          aria-label="Default select example"
                        >
                          <option selected>Select...</option>
                          <option value="1">One</option>
                          <option value="2">Two</option>
                          <option value="3">Three</option>
                        </select>
                      </div>
                      <div class="mb-3">
                        <label for="">Invoice</label>
                        <select
                          class="form-select"
                          aria-label="Default select example"
                        >
                          <option selected>Select...</option>
                          <option value="1">One</option>
                          <option value="2">Two</option>
                          <option value="3">Three</option>
                        </select>
                      </div>
                      <div class="mb-3">
                        <label for="">From</label>
                        <input type="date" class="form-control" />
                      </div>
                      <div class="mb-4">
                        <label for="">To</label>
                        <input type="date" class="form-control" />
                      </div>

                      <div class="flex-end">
                        <button class="btn mr-3" @click="toggleFilter">
                          Cancel
                        </button>
                        <button class="btn btn-primary" @click="toggleFilter">
                          Apply
                        </button>
                      </div>
                    </form>
                  </div>
                </div>
                <button
                  class="btn btn-primary btn-icon lft"
                  data-bs-toggle="modal"
                  data-bs-target="#newTaskModal"
                >
                  <i class="ic-plus"></i>
                  Add New Task
                </button>
              </div>
            </div>
            <div class="table-responsive">
              <table class="table table-01 table-striped table-action">
                <thead>
                  <th>
                    <div class="form-check">
                      <input
                        class="form-check-input"
                        type="checkbox"
                        value=""
                        id="checkbox-all"
                      />
                    </div>
                  </th>
                  <th>Title</th>
                  <th>Device</th>
                  <th>Status</th>
                  <th>Priority</th>
                  <th>Invoice Number</th>
                  <th>Pickup</th>
                  <th>Delivery</th>
                  <th>Action</th>
                </thead>
                <tbody>
                  <tr v-for="item in tableData" :key="item.index">
                    <td>
                      <div class="form-check">
                        <input
                          class="form-check-input"
                          type="checkbox"
                          value=""
                          id="checkbox-all"
                        />
                      </div>
                    </td>
                    <td>{{ item.name }}</td>
                    <td>
                      {{ item.device }}
                    </td>
                    <td>
                      {{ item.status }}
                    </td>
                    <td>
                      {{ item.priority }}
                    </td>
                    <td>
                      {{ item.invoice }}
                    </td>
                    <td>
                      {{ item.pickup }}
                    </td>
                    <td>
                      {{ item.delivery }}
                    </td>
                    <td>
                      <button class="btn btn-sm btn-icon-only">
                        <i class="ic-edit  text-cool-gray-600"></i>
                      </button>
                      <button class="btn btn-sm btn-icon-only">
                        <i class="ic-delete text-cool-gray-600"></i>
                      </button>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
            <Pagination />
          </div>
          <div class="modal-footer p-3 pt-0">
            <button
              type="button"
              class="btn"
              data-bs-toggle="modal"
              data-bs-target="#importTaskModal"
            >
              Import
            </button>
            <button
              type="button"
              class="btn btn-outline-primary"
              data-bs-dismiss="modal"
            >
              Cancel
            </button>
            <button type="button" class="btn btn-primary">
              Save
            </button>
          </div>
        </div>
      </div>
    </div>
    <div
      class="modal fade modal-01 "
      id="newTaskModal"
      tabindex="-1"
      aria-labelledby="newTaskModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h6>Create New Task</h6>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            ></button>
          </div>
          <div class="modal-body px-3">
            <div class="row">
              <div class="col-sm-6 mb-3">
                <label for="">Invoice Number</label>
                <select class="form-select" aria-label="Default select example">
                  <option selected>Select...</option>
                  <option value="1">One</option>
                  <option value="2">Two</option>
                  <option value="3">Three</option>
                </select>
              </div>
              <div class="col-sm-6 mb-3">
                <label for="">Title</label>
                <input type="text" class="form-control" />
              </div>
              <div class="col-sm-6 mb-3">
                <label for="">Invoice Number</label>
                <select class="form-select" aria-label="Default select example">
                  <option selected>Select...</option>
                  <option value="1">One</option>
                  <option value="2">Two</option>
                  <option value="3">Three</option>
                </select>
              </div>
              <div class="col-sm-6 mb-3">
                <label for="">Priority</label>
                <select class="form-select" aria-label="Default select example">
                  <option selected>Select...</option>
                  <option value="1">One</option>
                  <option value="2">Two</option>
                  <option value="3">Three</option>
                </select>
              </div>
              <div class="col-sm-6">
                <div class="p-3 bg-cool-gray-100">
                  <div class="mb-3">
                    <label for="">Pickup Address </label>
                    <div class="form-icon">
                      <input
                        type="text"
                        class="form-control"
                        id="validationTooltip01"
                        placeholder="Search..."
                      />
                      <i class="ic-search"></i>
                    </div>
                  </div>
                  <div class="mb-3">
                    <label for="">From</label>
                    <input
                      type="date"
                      class="form-control"
                      id="validationTooltip01"
                    />
                  </div>
                  <div>
                    <label for="">To</label>
                    <input
                      type="date"
                      class="form-control"
                      id="validationTooltip01"
                    />
                  </div>
                </div>
              </div>
              <div class="col-sm-6">
                <div class="p-3 bg-cool-gray-100">
                  <div class="mb-3">
                    <label for="">Pickup Address </label>
                    <div class="form-icon">
                      <input
                        type="text"
                        class="form-control"
                        id="validationTooltip01"
                        placeholder="Search..."
                      />
                      <i class="ic-search"></i>
                    </div>
                  </div>
                  <div class="mb-3">
                    <label for="">From</label>
                    <input
                      type="date"
                      class="form-control"
                      id="validationTooltip01"
                    />
                  </div>
                  <div>
                    <label for="">To</label>
                    <input
                      type="date"
                      class="form-control"
                      id="validationTooltip01"
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer p-3">
            <button type="button" class="btn" data-bs-dismiss="modal">
              Cancel
            </button>
            <button type="button" class="btn btn-primary">
              Save
            </button>
          </div>
        </div>
      </div>
    </div>
    <div
      class="modal fade modal-01"
      id="importTaskModal"
      tabindex="-1"
      aria-labelledby="importTaskModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h6>Import Files</h6>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            ></button>
          </div>
          <div class="modal-body px-3 flex-center-center flex-column py-5">
            <!-- <div class="w-100 border rounded p-3 align-center">
              <i class="ic-file text-primary h1"></i>
              <div class="flex-grow-1 ml-3">
                <div class="flex-center-between">
                  <p>Tasklist.csv</p>
                  <button class="btn btn-icon-only">
                    <i class="ic-close text-secondary small"></i>
                  </button>
                </div>
                <div class="progress my-1">
                  <div
                    class="progress-bar w-75"
                    role="progressbar"
                    aria-valuenow="75"
                    aria-valuemin="0"
                    aria-valuemax="100"
                  ></div>
                </div>
                <small class="text-cool-gray-600">5 MB of 9 MB</small>
              </div>
            </div> -->
            <i class="ic-file text-cool-gray-400 mb-2 h1"></i>
            <p class="font-semibold mb-2">Import your files into the system</p>

            <div class=" mb-2">
              <input
                type="file"
                class="custom-file-input"
                id="inputGroupFile01"
              />
            </div>
            <small class="text-cool-gray-600">example.csv, example.xls</small>
          </div>
          <div class="modal-footer p-3">
            <button type="button" class="btn" data-bs-dismiss="modal">
              Cancel
            </button>
            <button type="button" class="btn btn-primary">
              Save
            </button>
          </div>
        </div>
      </div>
    </div>
  </fragment>
</template>

<script>
  import Pagination from "../../Utility/Pagination";
  export default {
    name: "Task",
    components: { Pagination },
    data() {
      return {
        showFilter: false,

        tableData: [
          {
            id: 1,
            name: "Get ice",
            device: "3300",
            status: "In Progress",
            priority: "Low Piority",
            invoice: "INV-21-12-009",
            pickup: "Gairidhara, Kathmandu..",
            delivery: "Jawalakhel, Lalitpur",
          },
          {
            id: 2,
            name: "Reach Hadigaun",
            device: "3256",
            status: "Complete",
            priority: "High Priority",
            invoice: "INV-21-12-2011",
            pickup: "Jagati, Bhaktapur",
            delivery: "Banepa, Kavre",
          },
          {
            id: 3,
            name: "Get ice",
            device: "LU AAA 4813",
            status: "Opened",
            priority: "High Priority",
            invoice: "INV-21-12-2022",
            pickup: "Basundhara, Kathman...",
            delivery: "Banepa, Kavre",
          },
        ],
      };
    },
    methods: {
      toggleFilter() {
        this.showFilter = !this.showFilter;
      },
    },
  };
</script>
