<template>
  <Pie
    :chart-options="chartOptions"
    :chart-data="chartData"
    :chart-id="chartId"
    :dataset-id-key="datasetIdKey"
    :plugins="plugins"
    :css-classes="cssClasses"
    :styles="styles"
    :width="width"
    :height="height"
  />
</template>

<script>
  import { Pie } from "vue-chartjs/legacy";

  import {
    Chart as ChartJS,
    Title,
    Tooltip,
    Legend,
    ArcElement,
    CategoryScale,
  } from "chart.js";

  ChartJS.register(Title, Tooltip, Legend, ArcElement, CategoryScale);

  export default {
    name: "PieChart",
    components: {
      Pie,
    },
    props: {
      chartId: {
        type: String,
        default: "Pie-chart",
      },
      datasetIdKey: {
        type: String,
        default: "label",
      },
      width: {
        type: Number,
        default: 400,
      },
      height: {
        type: Number,
        default: 400,
      },
      cssClasses: {
        default: "",
        type: String,
      },
      styles: {
        type: Object,
        default: () => {},
      },
      plugins: {
        type: Array,
        default: () => [],
      },
    },
    data() {
      return {
        chartData: {
          labels: [
            "Move",
            "Idle",
            "Stop",
            "Offline",
            "Inactive",
            "Never Connected",
          ],
          datasets: [
            {
              backgroundColor: [
                "#41B883",
                "#fd7e14",
                "#EC7063",
                "#5DADE2",
                "#D7DBDD",
                "#AF7AC5",
              ],
              data: [5, 2, 3, 7, 5, 1],
            },
          ],
        },
        chartOptions: {
          responsive: true,
          maintainAspectRatio: false,
        },
      };
    },
  };
</script>
