<template>
  <div
    :class="pathName === '/' ? 'd-block' : 'd-none'"
    class="sidebar-main sidebar-vehicle"
  >
    <absolute-layout>
      <flex-layout>
        <div class="d-flex p-4">
          <i class="ic-car text-success h2"></i>
          <div class="mx-3 flex-grow-1">
            <h3>BA 23 KA 3241</h3>
            <p class="mt-1 text-cool-gray-600">24-12-2021 11:10:06 AM</p>
          </div>

          <a
            type="button"
            data-bs-toggle="offcanvas"
            data-bs-target="#offcanvasRight"
            aria-controls="offcanvasRight"
            class="h3 ic-settings text-cool-gray-600"
          >
          </a>
        </div>
        <ul class="tab-icons nav nav-pills" id="pills-tab" role="tablist">
          <li
            class="nav-item"
            role="presentation"
            data-bs-toggle="tooltip"
            data-bs-placement="top"
            title="Overview"
          >
            <a
              class="nav-link active"
              id="pills-overview-tab"
              data-bs-toggle="pill"
              data-bs-target="#pills-overview"
              type="button"
              role="tab"
              aria-controls="pills-overview"
              aria-selected="true"
            >
              <i class="ic-view"></i>
            </a>
          </li>
          <li
            class="nav-item"
            role="presentation"
            data-bs-toggle="tooltip"
            data-bs-placement="top"
            title="Activity"
          >
            <a
              class="nav-link"
              id="pills-activity-tab"
              data-bs-toggle="pill"
              data-bs-target="#pills-activity"
              type="button"
              role="tab"
              aria-controls="pills-activity"
              aria-selected="false"
            >
              <i class="ic-activity"></i>
            </a>
          </li>
          <li
            class="nav-item"
            role="presentation"
            data-bs-toggle="tooltip"
            data-bs-placement="top"
            title="Fuel"
          >
            <a
              class="nav-link"
              id="pills-fuel-tab"
              data-bs-toggle="pill"
              data-bs-target="#pills-fuel"
              type="button"
              role="tab"
              aria-controls="pills-fuel"
              aria-selected="false"
            >
              <i class="ic-fuel"></i>
            </a>
          </li>
        </ul>
        <div class="flex-grow-1 scrollable">
          <div class="tab-content py-3" id="pills-tabContent">
            <Overview />
            <Activity />
            <Fuel />
          </div>
        </div>
      </flex-layout>
    </absolute-layout>
    <portal to="offcanvas">
      <div
        class="offcanvas offcanvas-01 offcanvas-end"
        tabindex="-1"
        id="offcanvasRight"
        aria-labelledby="offcanvasRightLabel"
      >
        <div class="offcanvas-header pb-0">
          <div class="flex-center-between">
            <h5 id="offcanvasRightLabel">Widget Setting</h5>
            <button
              type="button"
              class="btn-close text-reset"
              data-bs-dismiss="offcanvas"
              aria-label="Close"
            ></button>
          </div>
          <p class="mt-3 text-cool-gray-600 align-center">
            <i class="ic-info mr-2"></i>Drag into order, add or remove data from
            here
          </p>
        </div>
        <div class="offcanvas-body scrollable">
          <draggable
            v-model="myArray"
            ghost-class="ghost"
            @end="onEnd"
            :move="onMove"
            handle=".handle"
            class="list-group"
          >
            <transition-group type="transition" name="flip-list">
              <form
                class="list-drag list-group-item"
                v-for="element in myArray"
                :key="element.id"
              >
                <div class="flex-center-between draggable">
                  <div class="align-center">
                    <i class="ic-dots handle"></i>
                    <small class="ml-2 font-semibold">
                      {{ element.name }}
                    </small>
                  </div>
                  <div class="form-check">
                    <input
                      :class="
                        'form-check-input ' +
                          (element.id === 1 ? 'widget-toggled' : '')
                      "
                      type="checkbox"
                      value=""
                      id="checkbox-all"
                    />
                  </div>
                </div>
                <div
                  class="flex-center-between"
                  v-for="item in element.widgetList"
                  :key="item.index"
                >
                  <div class="align-center">
                    <small class="ml-4 text-cool-gray-600">
                      {{ item.title }}
                    </small>
                  </div>
                  <div class="form-check">
                    <input
                      class="form-check-input"
                      type="checkbox"
                      value=""
                      id="checkbox-all"
                    />
                  </div>
                </div>
              </form>
            </transition-group>
          </draggable>
        </div>
        <div
          class="offcanvas-footer p-3 flex-end
      "
        >
          <button class="btn btn-ghost btn-sm mr-2">Cancel</button>
          <button class="btn btn-primary btn-sm">Apply</button>
        </div>
      </div>
    </portal>
  </div>
</template>

