<template>
  <flex-layout styleClasses="py-3">
    <div class="px-3">
      <div class="flex-center-between mb-3">
        <h6 class="text-primary">POI</h6>
        <div class="align-center">
          <div class="dropdown-01">
            <button
              class="btn btn-icon-only me-3"
              id="dropdownMenuButton1"
              data-bs-toggle="dropdown"
              aria-expanded="false"
            >
              <i class="ic-menu h6"></i>
            </button>
            <ul class="dropdown-menu" aria-labelledby="dropdownMenuButton1">
              <li>
                <a
                  class="dropdown-item"
                  href="#"
                  data-bs-toggle="modal"
                  data-bs-target="#editAlertModal"
                >
                  Export</a
                >
              </li>
              <li>
                <a class="dropdown-item" href="#">Import</a>
              </li>
            </ul>
          </div>
          <div class="dropdown-01">
            <button
              class="btn btn-primary p-1"
              id="dropdownMenuButton1"
              data-bs-toggle="dropdown"
              aria-expanded="false"
            >
              <i class="ic-plus"></i>
            </button>

            <ul class="dropdown-menu" aria-labelledby="dropdownMenuButton1">
              <li>
                <a
                  class="dropdown-item"
                  href="#"
                  data-bs-toggle="modal"
                  data-bs-target="#editAlertModal"
                  >Add New Groups</a
                >
              </li>
              <li>
                <router-link class="dropdown-item" to="add-poi"
                  >Create New POI</router-link
                >
              </li>
            </ul>
          </div>
        </div>
      </div>
      <small class="my-3 text-cool-gray-600 d-flex">
        <i class="ic-info mr-2 h6"></i> You can add, delete or edit these zone
        acording to your preferences.
      </small>
      <div class="form-icon mb-3 ">
        <input
          type="Email"
          class="form-control"
          id="validationTooltip01"
          placeholder="Search..."
        />
        <i class="ic-search"></i>
      </div>
    </div>
    <div class="px-3 scrollable">
      <div class="collapse-01 spacing__vertical flex-grow-1 scrollable">
        <div
          v-for="data in groupList"
          :key="data.index"
          class="collapse-01-item"
        >
          <div
            :class="'collapse-01-toggler ' + (data.id == 1 ? 'active' : '')"
            @click="HandlerFunction"
          >
            <div class="form-check">
              <input
                class="form-check-input"
                type="checkbox"
                value=""
                id="checkbox-group-01"
              />
              <label class="form-check-label" for="checkbox-group-01"> </label>
            </div>
            <a
              class="title flex-grow-1"
              data-bs-toggle="collapse"
              :href="'#collapse-group-' + data.id"
              role="button"
              :aria-expanded="data.id == 1 ? 'true' : 'false'"
              :aria-controls="'collapse-group-' + data.id"
            >
              {{ data.title }}
            </a>
          </div>

          <ul
            class="list-alert collapse"
            :class="data.id == 1 ? 'show' : ''"
            :id="'collapse-group-' + data.id"
          >
            <li v-for="item in data.groupItems" :key="item.index">
              <div class="form-check">
                <input
                  class="form-check-input"
                  type="checkbox"
                  value=""
                  id="checkbox-all"
                />
                <label class="form-check-label" for="checkbox-all">
                  {{ item.title }}
                </label>
              </div>
              <div class="action">
                <button
                  class="btn btn-icon-only me-2"
                  data-bs-toggle="modal"
                  data-bs-target="#editAlertModal"
                >
                  <i class="ic-edit"></i>
                </button>
                <button class="btn btn-icon-only">
                  <i class="ic-delete"></i>
                </button>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <portal to="modal">
      <div
        class="modal fade modal-01"
        id="editAlertModal"
        tabindex="-1"
        aria-labelledby="editAlertModalLabel"
        aria-hidden="true"
      >
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-header">
              <h6>Add New Group</h6>
              <button
                type="button"
                class="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <div class="modal-body px-3">
              <div class="">
                <label for="">Title</label>
                <input
                  type="tetx"
                  class="form-control"
                  id="validationTooltip01"
                />
              </div>
              <!-- <div class="">
                <label for="">POI</label>
                <div class="form-icon">
                  <input
                    type="text"
                    class="form-control"
                    id="validationTooltip01"
                  />
                  <i class="ic-search"></i>
                </div>
              </div> -->
            </div>
            <div class="modal-footer p-3">
              <button type="button" class="btn" data-bs-dismiss="modal">
                Cancel
              </button>
              <button type="button" class="btn btn-primary">
                Save
              </button>
            </div>
          </div>
        </div>
      </div>
    </portal>
  </flex-layout>
</template>

<script>
  import FlexLayout from "../../../components/Layout/FlexLayout";

  export default {
    name: "POI",
    components: { FlexLayout },

    data() {
      return {
        groupList: [
          {
            id: "1",
            title: "Baneshwor group (20)",
            groupItems: [
              {
                title: "LU A A A 4813",
              },
              {
                title: "OBD 9801108608",
              },
              {
                title: "OBD Testing",
              },
            ],
          },
          {
            id: "2",
            title: "Bhaktapur group (20)",
            groupItems: [
              {
                title: "LU A A A 4813",
              },
              {
                title: "OBD 9801108608",
              },
              {
                title: "OBD Testing",
              },
            ],
          },
        ],
      };
    },

    methods: {
      HandlerFunction(event) {
        event.target.parentElement.classList.toggle("active");
      },
    },
  };
</script>
